@import 'master';

@media (max-width: 1200px) {
  .section-hero {
    min-height: 0;
  }
}

@media (max-width: 992px) {
  .header {
    background: lighten($color_main, 32%);
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    animation: fadeIn 0.5s ease-in both;
  }
  .plan-steps {
    justify-content: center;
  }
}

@media (max-width: 768px) {
  body {
    font-size: 14px;
  }
  .section-plan {
    background-image: none;
  }
  .plan-steps {
    &__item {
      margin-right: 0;
      padding: .75em 0.75em;
      &-text {
        width: 125px;
      }
    }
    &__icon {
      transform: scale(0.8);
    }
  }
}

@media (max-width: 576px) {
  body {
    font-size: 12px;
  }

  .dialog {
    padding: 1.75em 2.25em;

    &__form {
      padding: 0 0.5em;
    }
  }

  .section-title {
    &-wrapper {
      font-size: 10px;
    }
  }

  .header {
    padding: 0;

    &__logo {
      width: 80px;
    }
  }

  .hero-form {
    border: 6px solid $color_main;
    border-radius: 48px;
    padding: 30px 15px;
  }

  .section-hero {
    padding-top: 80px;
    background-position: center right;
    min-height: 0;
  }
}